import React, { useEffect, useState } from 'react'
import { Link } from "react-router-dom";
import axios from 'axios'
import { API } from '../Pages/Home/Home'
var uniqid = require("uniqid")
const id = uniqid()

function Politics() {

  const [politics, setPolitics] = useState([])

  const options = {
    method: 'GET',
    url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
    params: {
      q: 'politics',
      pageNumber: '1',
      pageSize: '5',
      autoCorrect: 'true',
      fromPublishedDate: 'null',
      toPublishedDate: 'null'
    },


    headers: {
      'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
    }
  };

  useEffect(() => {

    axios.request(options).then((response) => {
      setPolitics(response.data.value)
      console.log(response.data.value);
    }).catch(function (error) {
      console.error(error);
    });

  }, [])
  return (
    <React.Fragment>
      <div className="section-title">
        <h4 className="m-0 text-uppercase font-weight-bold">
          politics
        </h4>
        <a
          className="text-secondary font-weight-medium text-decoration-none"
          
        >
          View All
        </a>
      </div>
      {politics.slice(0, 1).map((obj) => (
        <div onClick={() =>{         
          window.sessionStorage.setItem(id,JSON.stringify(obj)) 
         }} key={id} className="col-lg-12">
          <Link to={`/single/${id}`} >  <div className="row news-lg mx-0 mb-3">
            <div className="col-md-6 h-100 px-0">
              <img
                className="img-fluid h-100"
                src={obj.image.url}
                style={{ objectFit: "cover" }}
              />
            </div>
            <div className="col-md-6 d-flex flex-column border bg-white h-100 px-0">
              <div className="mt-auto p-4">
                <div className="mb-2">
                  <a
                    className="badge badge-primary text-uppercase font-weight-semi-bold p-2 mr-2"
                    
                  >
                    Politics
                  </a>
                  <a className="text-body" >
                    <small>Jan 01, 2045</small>
                  </a>
                </div>
                <a
                  className="h4 d-block mb-3 text-secondary text-uppercase font-weight-bold"
                  
                >
                  {obj.title}

                </a>
                {/* <p className="m-0">
                          Dolor lorem eos dolor duo et eirmod sea. Dolor sit magna
                          rebum clita rebum dolor stet amet justo
                        </p> */}
              </div>
              <div className="d-flex justify-content-between bg-white border-top mt-auto p-4">
                <div className="d-flex align-items-center">
                  <img
                    className="rounded-circle mr-2"
                    src="img/user.jpg"
                    width={25}
                    height={25}

                  />
                  <small>John Doe</small>
                </div>
                <div className="d-flex align-items-center">
                  <small className="ml-3">
                    <i className="far fa-eye mr-2" />
                    12345
                  </small>
                  <small className="ml-3">
                    <i className="far fa-comment mr-2" />
                    123
                  </small>
                </div>
              </div>
            </div>
          </div> </Link>
        </div>))}
      {politics.slice(1, 5).map((obj) => (

        <div className="col-lg-6">
          <div
            className="d-flex align-items-center bg-white mb-3"
            style={{ height: "fitContent" }}
          >
            <img className="img-fluid" src={obj.image.url} style={{ maxWidth: 100, minWidth: 100, minHeight: 100, maxHeight: 100 }} />
            <div className="w-100 h-100 px-3 d-flex flex-column justify-content-center border border-left-0">
              <div className="mb-2">
                <a
                  className="badge badge-primary text-uppercase font-weight-semi-bold p-1 mr-2"
                  
                >
                  politics
                </a>
                <a className="text-body" >
                  <small>Jan 01, 2045</small>
                </a>
              </div>
              <a
                className="h6 m-0 text-secondary text-uppercase font-weight-bold"
                
              >
                {obj.title}
              </a>
            </div>
          </div>

        </div>))}
      {/* <div className="col-lg-6">
               
                  <div
                    className="d-flex align-items-center bg-white mb-3"
                    style={{ height: 110 }}
                  >
                    <img className="img-fluid" src="img/news-110x110-4.jpg" />
                    <div className="w-100 h-100 px-3 d-flex flex-column justify-content-center border border-left-0">
                      <div className="mb-2">
                        <a
                          className="badge badge-primary text-uppercase font-weight-semi-bold p-1 mr-2"
                          
                        >
                          Business
                        </a>
                        <a className="text-body" >
                          <small>Jan 01, 2045</small>
                        </a>
                      </div>
                      <a
                        className="h6 m-0 text-secondary text-uppercase font-weight-bold"
                        
                      >
                        Lorem ipsum dolor sit amet elit...
                      </a>
                    </div>
                  </div>
                </div> */}
    </React.Fragment>
  )
}

export default Politics
