import React, { useEffect, useState } from 'react'
import axios from 'axios'
import NewsLetter from '../NewsLetter/NewsLetter'
import SocialFollow from '../SocialFollow'
import Health from '../Health/Health'
import Politics from '../Politics/Politics'
import {Link} from "react-router-dom";

var uniqid = require("uniqid")
const id =uniqid()

function NewsWithSideBar() {
 
  const [market, setMarket] = useState([])
 
 const options = {
  method: 'GET',
  url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
  params: {
    q: ' stockmarket',
    pageNumber: '1',
    pageSize: '9',
    autoCorrect: 'true',
    fromPublishedDate: 'null',
    toPublishedDate: 'null'
  },


    headers: {
      'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
    }
  };
  
  useEffect(() => {
    
    axios.request(options).then((response)=> {
      setMarket(response.data.value)
      console.log( 'market', response.data.value);
    }).catch(function (error) {
      console.error(error);
    });
    
  }, [])
 


 

  return (
    
    <React.Fragment>
      {/* News With Sidebar Start */}
      <div className="container-fluid">
        <div className="container">
          <div className="row">
            <div className="col-lg-8">
              <div className="row">
                <div className="col-12">
                  <div className="section-title">
                    <h4 className="m-0 text-uppercase font-weight-bold">
                      Market
                    </h4>
                    <a
                      className="text-secondary font-weight-medium text-decoration-none"
                      
                    >
                      View All
                    </a>
                  </div>
                </div>

                {market.slice(0,4).map((obj,index) => {
                 const { title}=obj

        return  ( <div key={index} className="col-lg-6">
                <Link to={`/single/${id}`} > <div onClick={() =>{         /*Here, the unique Id is generated*/
                  window.sessionStorage.setItem(id,JSON.stringify(obj))   /*with the help of an NPM package called uniqId*/
                 }}  className="position-relative mb-3">
                      <img
                        className="img-fluid w-100"
                        src= {obj.image.url} 
                        
                        style={{ objectFit: "cover" }}
                      />
                      <div className="bg-white border border-top-0 p-4">
                        <div className="mb-2">
                          <a
                            className="badge badge-primary text-uppercase font-weight-semi-bold p-2 mr-2"

                          >
                            Business
                          </a>
                          <a className="text-body" >
                            <small>Jan 01, 2045</small>
                          </a>
                        </div>
                        <a
                          className="h4 d-block mb-0 text-secondary text-uppercase font-weight-bold"
                          
                        >
                          {title} 
                        </a>
                       
                      </div>
                      
                      <div className="d-flex justify-content-between bg-white border border-top-0 p-4">
                        <div className="d-flex align-items-center">
                          {/* <img
                      className="rounded-circle mr-2"
                      src="img/user.jpg"
                      width={25}
                      height={25}
                      
                    /> */}
                          <small>{obj.provider.name}</small>
                        </div>
                        {/* <div className="d-flex align-items-center">
                    <small className="ml-3">
                      <i className="far fa-eye mr-2" />
                      12345
                    </small>
                    <small className="ml-3">
                      <i className="far fa-comment mr-2" />
                      123
                    </small>
                  </div> */}
                      </div>
                    </div></Link>
                  </div> )})}


                {market.slice(4,8).map((obj,index)=> (
                <div onClick={() =>{         
                  window.sessionStorage.setItem(id,JSON.stringify(obj))  
                 }} key={index} className="col-lg-6">
                <Link to={`/single/${id}`} > <div
                    className="d-flex align-items-center bg-white mb-3"
                    style={{ height: 110 }}
                  >
                    <img className="img-fluid" src={obj.image.url} style={{ maxWidth:100,minWidth:100 ,minHeight:100,maxHeight:100 }} alt />
                    <div className="w-100 h-100 px-3 d-flex flex-column justify-content-center border border-left-0">
                      <div className="mb-2">
                        <a
                          className="badge badge-primary text-uppercase font-weight-semi-bold p-1 mr-2"
                          
                        >
                          Business
                        </a>
                        <a className="text-body" >
                          <small>Jan 01, 2045</small>
                        </a>
                      </div>
                      <a
                        className="h6 m-0 text-secondary text-uppercase font-weight-bold"
                        
                      >
                        {obj.title}
                      </a>
                    </div>
                  </div></Link>
                </div>))}

                <Politics/>
                
                
              </div>
            </div>
            <div className="col-lg-4">
              
              {/* Ads Start */}
              <div className="mb-3">
                <div className="section-title mb-0">
                  <h4 className="m-0 text-uppercase font-weight-bold">
                    Advertisement
                  </h4>
                </div>
                <div className="bg-white text-center border border-top-0 p-3">
                  <a >
                   
                  </a>
                </div>
              </div>
              {/* Ads End */}
              <Health/>
              <NewsLetter />
              <SocialFollow />              
              {/* Tags Start */}
              <div className="mb-3">
                <div className="section-title mb-0">
                  <h4 className="m-0 text-uppercase font-weight-bold">Tags</h4>
                </div>
                <div className="bg-white border border-top-0 p-3">
                  <div className="d-flex flex-wrap m-n1">
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Politics
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Business
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Corporate
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Business
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Health
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Education
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Science
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Business
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Foods
                    </a>
                    <a  className="btn btn-sm btn-outline-secondary m-1">
                      Travel
                    </a>
                  </div>
                </div>
              </div>
              {/* Tags End */}
            </div>
          </div>
        </div>
      </div>
      {/* News With Sidebar End */}
    </React.Fragment>
    
  )
}


export default NewsWithSideBar
