import React,{ useState} from 'react'
import Health from '../../Health/Health';
import Navbar from '../../NavBar/Navbar';
import SocialFollow from '../../SocialFollow';
import Topbar from '../../Topbar/Topbar';
import { css } from "@emotion/react";
import ScaleLoader from "react-spinners/ScaleLoader"
import {Link} from "react-router-dom"
import Footer from '../../Footer/Footer';
var uniqid = require("uniqid")
const uniqId =uniqid()

function KWrdResult(props) {
    const [loading, setLoading]= useState(false)
         
        const {id}= props.match.params
        const objFrmSession = [sessionStorage.getItem(id)]
        const data = JSON.parse(objFrmSession)
        console.log('data', id , data);

    return (
        <React.Fragment>

            <div>
                <Topbar />
                <Navbar />
                {/* News With Sidebar Start */}
                <div className="container-fluid">
                    <div className="container">
                        <div className="row">
                {data &&        data.map((obj)=>(  
                         <div className=" pt-2 col-lg-8">

                                {/* News Start */}
                     
                                <Link to={`/single/${id}`} >    <div 
                                onClick={() =>{         
                                    window.sessionStorage.setItem(id,JSON.stringify(obj)) 
                                   }} key={uniqId}
                                className="card mb-3" style={{ maxWidth: 'fitContent' }}>
                                    <div className="row no-gutters">
                                        <div className="col-md-3">
                                            <img className="bd-placeholder-img" src={obj.image.url}  width="100%" height='100%' xmlns="http://www.w3.org/2000/svg" aria-label="Placeholder: Image" preserveAspectRatio="xMidYMid slice" role="img"></img> 
                                        </div>
                                        <div className="col-md-8">
                                            <div className="card-body">
                                                <h5 className="card-title">{obj.title}</h5>
                                                <p className="card-text">{obj.description}</p>
                                                <p className="card-text"><small className="text-muted">Last updated 3 mins ago</small></p>
                                            </div>
                                        </div>
                                    </div>
                                </div> </Link> 
                                {/* News End */}

                            </div>    ))}
                            <div className="col-lg-4">
                              
                               <Health />
                                <SocialFollow />
                                {/* Ads Start */}
                                {/* <div className="mb-3">
                                    <div className="section-title mb-0">
                                        <h4 className="m-0 text-uppercase font-weight-bold">Advertisement</h4>
                                    </div>
                                    <div className="bg-white text-center border border-top-0 p-3">
                                        <a ><img className="img-fluid" src="img/news-800x500-2.jpg" alt="" /></a>
                                    </div>
                                </div> */}
                                {/* Ads End */}
                                {/* Popular News Start */}
                               
                                {/* Newsletter Start */}
                                <div className="mb-3">
                                    <div className="section-title mb-0">
                                        <h4 className="m-0 text-uppercase font-weight-bold">Newsletter</h4>
                                    </div>
                                    <div className="bg-white text-center border border-top-0 p-3">
                                        <p>Aliqu justo et labore at eirmod justo sea erat diam dolor diam vero kasd</p>
                                        <div className="input-group mb-2" style={{ width: '100%' }}>
                                            <input type="text" className="form-control form-control-lg" placeholder="Your Email" />
                                            <div className="input-group-append">
                                                <button className="btn btn-primary font-weight-bold px-3">Sign Up</button>
                                            </div>
                                        </div>
                                        <small>Lorem ipsum dolor sit amet elit</small>
                                    </div>
                                </div>
                                {/* Newsletter End */}
                                {/* Tags Start */}
                                <div className="mb-3">
                                    <div className="section-title mb-0">
                                        <h4 className="m-0 text-uppercase font-weight-bold">Tags</h4>
                                    </div>
                                    <div className="bg-white border border-top-0 p-3">
                                        <div className="d-flex flex-wrap m-n1">
                                            <a className="btn btn-sm btn-outline-secondary m-1">Politics</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Business</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Corporate</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Business</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Health</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Education</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Science</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Business</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Foods</a>
                                            <a className="btn btn-sm btn-outline-secondary m-1">Travel</a>
                                        </div>
                                    </div>
                                </div>
                                {/* Tags End */}
                            </div>
                        </div>
                    </div>
                </div>
                {/* News With Sidebar End */}
                <Footer/>
                {/* Back to Top */}
                <a className="btn btn-primary btn-square back-to-top"><i className="fa fa-arrow-up" /></a>
                {/* JavaScript Libraries */}
                {/* Template Javascript */}
            </div>


        </React.Fragment>
    )
}

export default KWrdResult
