import React, { useState, useEffect } from 'react'
import ScaleLoader from "react-spinners/ScaleLoader";
import { css } from "@emotion/react";
import axios from 'axios'
import {API} from '../Pages/Home/Home'
import {Link} from "react-router-dom";
var uniqid = require("uniqid")
const id =uniqid()
function FeaturedNews() {
  const[loading,setLoading] = useState(false)
  const [featured, setFeatured] = useState([])
  const options = {
    method: 'GET',
    url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
    params: {
      q: 'technology',
      pageNumber: '1',
      pageSize: '15',
      withThumbnails: 'true',
      autoCorrect: 'true',
      fromPublishedDate: 'null',
      toPublishedDate: 'null'
    },
    headers: {
      'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
    }
  };
  useEffect(() => {
    setLoading(true)
    axios.request(options).then((response)=> {
      setFeatured(response.data.value);
      setLoading(false)
    }).catch(function (error) {
      console.error(error);
    });
    
  },[])

  const override = css`
  display: block;
  margin: 0 auto;
  border-color: yellow;
`;
  return (
    <React.Fragment>
      {/* Featured News Slider Start */}
      <div className="container-fluid pt-5 mb-3">
        <div className="container">
          <div className="section-title">
            <h4 className="m-0 text-uppercase font-weight-bold">Featured News</h4>
          </div>
          <div style={{ display: 'flex', overflow: 'auto' }} className=" owl-carousel news-carousel carousel-item-4  position-relative ">
          {loading? <ScaleLoader size={90} css={override} color='#f5e100' loading={loading} />:featured.map((obj,index) => /*The slice() method returns a shallow copy of a portion of an array into a new array object selected from start to end (end not included) where start and end represent the index of items in that array. */
              <div onClick={() =>{         
                window.sessionStorage.setItem(id,JSON.stringify(obj)) 
               }} key={index}
                className="position-relative overflow-hidden col-lg-3  col-md-6 col-sm-9 col-11"
                style={{ height: 300, display: 'inline-block' }}
              >
         <Link to={`/single/${id}`} >        <img
                  className="img-fluid h-100  "
                  src={obj.image.url}
                  style={{ objectFit: "cover" }}
                />
                <div className="overlay">
                  <div className="mb-2">
                    <a
                      className="badge badge-primary text-uppercase font-weight-semi-bold p-2 mr-2"

                    >
                      Tech
                    </a>
                    <a className="text-white" >
                      <small>Jan 01, 2045</small>
                    </a>
                  </div>
                  <a
                    className="h6 m-0 text-white text-uppercase font-weight-semi-bold"
                    
                  >
                    {obj.title}
                  </a>
                </div> </Link>
              </div>  )}
          </div>
        </div>
      </div>
      {/* Featured News Slider End */}
    </React.Fragment>
  )
}

export default FeaturedNews
