import React,{useState,useEffect} from 'react'
import '../Pages/Home/style.css'
import'../Pages/Home/style.min.css'
import SearchInput from './SearchInput'
import axios from 'axios'
import {useHistory} from 'react-router-dom'
import { css } from "@emotion/react";
import ScaleLoader from "react-spinners/ScaleLoader"
var uniqid = require("uniqid")
const id =uniqid()

function Navbar() {
  const [keyWrd,setKeyWrd]= useState([])
    /*Date_Setters*/ 
 var today = new Date()
 let curTime =  today.getHours() + ':' + today.getMinutes() + ', ' 
 let curDate= today.getDate() + ', ' 
 let curYear = today.getUTCFullYear()

 const monthNames = ["January", "February", "March", "April", "May", "June",
 "July", "August", "September", "October", "November", "December"
];

const d = new Date();

/*DayNamers*/
 const dayNames = ["Sun","Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
const dN=new Date();
/*Date_Setters*/



const [loading,setLoading] = useState(false)
const [result,setResult] = useState([])
const [clicked,setClicked] = useState(false)
const history = useHistory()
  const options = {
    method: 'GET',
    url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
    params: {
      q: keyWrd,
      pageNumber: '1',
      pageSize: '15',
      withThumbnails: 'true',
      autoCorrect: 'true',
      fromPublishedDate: 'null',
      toPublishedDate: 'null'
    },
    headers: {
      'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
    }
  };
  const override = css`
  display: block;
  margin: 0 auto;
  border-color: yellow;
`;

  const handleSubmit = (e)=>{
   e.preventDefault();
   setClicked(true);
  }

  useEffect(() => {
  
    axios.request(options).then((res)=> {
      setResult(res.data.value)
      window.sessionStorage.setItem(id,JSON.stringify(result))
     }).then(()=>{
      history.push(`/searchResults/${id}`)
      setClicked(false);
    }).catch(function (error) {
       console.error(error);
     })
  }, [clicked]);

  const [isNavCollapsed, setIsNavCollapsed] = useState(true);

  const handleNavCollapse = () => setIsNavCollapsed(!isNavCollapsed);
console.log(isNavCollapsed);
    return (
        <div>
             {/* Navbar Start */}
  <div className="container-fluid p-0">
    <nav className="navbar navbar-expand-lg bg-dark navbar-dark py-2 py-lg-0 px-lg-5">
      <a href='/' className="navbar-brand d-block d-lg-none">
        <h1 className="m-0 display-4  text-primary">
          b<span className="text-white font-weight-normal">rief</span>      
        </h1>
      </a>
      <a className="nav-link text-body small " >
              {curTime} {(dayNames[dN.getDay()])}, {(monthNames[d.getMonth()])} {curDate} {curYear}
              </a>
      <button
        type="button"
        className="navbar-toggler"
        data-toggle="collapse"
        data-target="#navbarCollapse"
        onClick={handleNavCollapse}
      >
        <span className="navbar-toggler-icon" />
      </button>
      <div
        className="collapse navbar-collapse justify-content-between px-0 px-lg-3"
        id="navbarCollapse"
      >
        <div className="navbar-nav mr-auto py-0">
          <a href='/' className="nav-item nav-link active">
            Home
          </a>
          <a  className="nav-item nav-link">
            Category
          </a>

          <div className="nav-item dropdown">
            <a
              
              className="nav-link dropdown-toggle"
              data-toggle="dropdown"
            >
              Dropdown
            </a>
            <div className="dropdown-menu rounded-0 m-0">
              <a  className="dropdown-item">
                Menu item 1
              </a>
              <a  className="dropdown-item">
                Menu item 2
              </a>
              <a  className="dropdown-item">
                Menu item 3
              </a>
            </div>
          </div>
          <a href="/contact" className="nav-item nav-link">
            Contact
          </a>
        </div>
    <SearchInput/>
      </div>
    </nav>
    <div className={`${isNavCollapsed ? 'collapse' : ''} navbar-collapse bg-secondary col-lg-1 `} id="navbarsExample09">
        <a className="nav-link text-info" href="/contact">Contact</a>
        <a className="nav-link text-info" href="/login">Login</a>
 
         <div>
           <form onSubmit={handleSubmit}>
             <input
            type="text"
            onChange={(e)=>setKeyWrd(e.target.value)}
            className="form-control border-0"
            placeholder="Keyword Search"
            style={{width:'80%' }}
          />
          <div className="input-group-append">
            <button type='submit' style={{float: 'left'}} className="input-group-text bg-primary text-dark border-0 px-3">
              <i className="fa fa-search" />
            </button>
          </div>
          </form>
      </div> 
      </div>
      </div>
  
  {/* Navbar End */}
        </div>
    )
}

export default Navbar;
