import React, { useState,useEffect } from 'react'
import ScrollMenu from 'react-horizontal-scrolling-menu';
import ScaleLoader from "react-spinners/ScaleLoader";
import { css } from "@emotion/react";
import {Link} from "react-router-dom";
import axios from 'axios'
import BreakingNewsBar from '../BreakingNewsBar/BreakingNewsBar';
import {API} from '../Pages/Home/Home'
var uniqid = require("uniqid")
const id =uniqid()

function MainNewsSlider() {
  let [loading, setLoading] = useState(false)
  const [narticles, setNarticles] = useState([])

  const override = css`
  display: block;
  margin: 0 auto;
  border-color: yellow;
`;
 const options = {
  method: 'GET',
  url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/TrendingNewsAPI',
  params: {pageNumber: '1', pageSize: '7', withThumbnails: 'false', location: 'in'},


    headers: {
      'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
    }
  };
  
  useEffect(() => {
    setLoading(true)
    axios.request(options).then((response)=> {
      setNarticles(response.data.value)

        setLoading(false)
      
    }).catch(function (error) {
      console.error(error);
    });
    
  }, [])
 

  return (
    <React.Fragment>
    <div>
      {/* Main News Slider Start */}
       <div className="container-fluid">
        <div className="row">
          <div className="col-lg-7 px-0">
            <div style={{ display: 'flex', overflow: 'auto' }} className="owl-carousel main-carousel position-relative">


              {loading? <ScaleLoader size={90} css={override} color='#f5e100' loading={loading} />: narticles.slice(0, 3).map((obj,index) => (
               <div  onClick={() =>{         
                window.sessionStorage.setItem(id,JSON.stringify(obj)) 
               }} key={index}
                className="position-relative overflow-hidden  col-12"
                style={{ height: 500 }}
              >
             <Link to={`/single/${id}`} >   <img
                  className="img-fluid h-100"
                  src={obj.image.url}
                  style={{ objectFit: "cover" }}
                  alt='Image Not Available At This Time.'

                />
                <div className="overlay">
                  <div className="mb-2">
                    <a
                      className="badge badge-primary text-uppercase font-weight-semi-bold p-2 mr-2"
                      
                    >
                      Top Headlines
                    </a>
                    <a className="text-white" >
                      {obj.datePublished}
                    </a>
                  </div>
                  <a
                    className="h2 m-0 text-white text-uppercase font-weight-bold"
                    
                  >
                    {obj.title}
                  </a>
                </div> </Link>
              </div> ))}

            </div>
          </div>
          <div className="col-lg-5 px-0">
            <div className="row mx-0">



              {narticles.slice(3, 7).map((obj,index) => (
            <div  onClick={() =>{         
              window.sessionStorage.setItem(id,JSON.stringify(obj)) 
             }} key={index} className="col-md-6 px-0">
      <Link to={`/single/${id}`} >        <div
           
                  className="position-relative overflow-hidden"
                  style={{ height: 250 }}
                >
                  <img
                    className="img-fluid w-100 h-100"
                    src={obj.image.url}
                    style={{ objectFit: "cover" }}
                    alt='Image Not Available At This Time.'

                  />
                  <div className="overlay">
                    <div className="mb-2">
                      <a
                        className="badge badge-primary text-uppercase font-weight-semi-bold p-2 mr-2"
                        
                      >
                        Top Headlines
                      </a>
                      <a className="text-white" >
                        <small>Jan 01, 2045</small>
                      </a>
                    </div>
                    <a
                      className="h6 m-0 text-white text-uppercase font-weight-semi-bold"
                      
                    >
                      {obj.title}
                    </a>
                  </div>
                </div> </Link>
              </div>  ))}

            </div>
          </div>
        </div>
      </div>
      {/* Main News Slider End */}
      <BreakingNewsBar/>
    </div> 
    </React.Fragment>
  )
  
}

export default MainNewsSlider
