import React,{useState,useEffect }from 'react'
import axios from 'axios'
import {useHistory} from 'react-router-dom'
import { css } from "@emotion/react";
import ScaleLoader from "react-spinners/ScaleLoader"
var uniqid = require("uniqid")
const id =uniqid()

function SearchInput() {

const [loading,setLoading] = useState(false)
const [keyWrd1,setKeyWrd1]= useState([])
const [result,setResult] = useState([])
const [clicked,setClicked] = useState(false)
const history = useHistory()
  const options = {
    method: 'GET',
    url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
    params: {
      q: keyWrd1,
      pageNumber: '1',
      pageSize: '15',
      withThumbnails: 'true',
      autoCorrect: 'true',
      fromPublishedDate: 'null',
      toPublishedDate: 'null'
    },
    headers: {
      'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
      'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
    }
  };
  const override = css`
  display: block;
  margin: 0 auto;
  border-color: yellow;
`;

  const handleSubmit = (e)=>{
   e.preventDefault();
   setClicked(true);
  }

  useEffect(() => {
  
    axios.request(options).then((res)=> {
      setResult(res.data.value)
      window.sessionStorage.setItem(id,JSON.stringify(result))
     }).then(()=>{
      history.push(`/searchResults/${id}`)
      setClicked(false);
    }).catch(function (error) {
       console.error(error);
     })
  }, [clicked]);

  return (
    <React.Fragment>
{loading ? <ScaleLoader size={90} css={override} color='#f5e100' loading={loading} />:  <form onSubmit={handleSubmit}>
      <div
        className="input-group ml-auto d-none d-lg-flex"
        style={{ width: "100%", maxWidth: 300 }}
      >
        <input
          type="text"
          onChange={(e)=>{setKeyWrd1(e.target.value)}}
          className="form-control border-0"
          placeholder="Keyword"
        />
        <div className="input-group-append">
          <button type='submit' className="input-group-text bg-primary text-dark border-0 px-3">
            <i className="fa fa-search" />
          </button>
        </div>
      </div>
        </form>}
    </React.Fragment>
  )
}

export default SearchInput
