import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { API } from '../Pages/Home/Home'
var uniqid = require("uniqid")
const id =uniqid()
function Health() {
  const [loading, setLoading] = useState(false)
  const [health, setHealth] = useState([])
  const [fitness, setFitness] = useState([])
  var andSymbol = '&'

  const options = {
    method: 'GET',
    url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
    params: {
      q: 'health',
      pageNumber: '1',
      pageSize: '10',
      autoCorrect: 'true',
      fromPublishedDate: 'null',
      toPublishedDate: 'null'
    },
  
  
      headers: {
        'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
        'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
      }
    };

    const options2 = {
      method: 'GET',
      url: 'https://contextualwebsearch-websearch-v1.p.rapidapi.com/api/search/NewsSearchAPI',
      params: {
        q: 'fitness',
        pageNumber: '1',
        pageSize: '10',
        autoCorrect: 'true',
        fromPublishedDate: 'null',
        toPublishedDate: 'null'
      },
    
    
        headers: {
          'x-rapidapi-key': 'fbdff7e1bdmsh0d25a8cd1b29338p1ee46bjsn8d137e63eddc',
          'x-rapidapi-host': 'contextualwebsearch-websearch-v1.p.rapidapi.com'
        }
      };
    
    useEffect(() => {
      
      axios.request(options).then(function (response) {
        setHealth(response.data.value)
        console.log(response.data.value);
      }).catch(function (error) {
        console.error(error);
      }).then(()=> {


        
        axios.request(options2).then(function (response) {
          setFitness(response.data.value)
          console.log(response.data.value);
        }).catch(function (error) {
          console.error(error);
        });
      })
        
    }, [])
  return (
    <React.Fragment>
      {/* Popular News Start */}
      <div className="mb-3">
        <div className="section-title mb-0">
          <h4 className="m-0 text-uppercase font-weight-bold">
            Health {andSymbol} Fitness
          </h4>
        </div>
        <div className="bg-white border border-top-0 p-3">

          {health.slice(0, 3).map((obj) => (
            <div
              className="d-flex align-items-center bg-white mb-3"
              style={{ height: 'fitContent' }}
            >
              <img className="img-fluid" src={obj.image.url} style={{ maxHeight: 100, maxWidth: 100, minHeight: 100, minWidth: 100 }} />
              <div className="w-100 h-100 px-3 d-flex flex-column justify-content-center border border-left-0">
                <div className="mb-2">
                  <a
                    className="badge badge-primary text-uppercase font-weight-semi-bold p-1 mr-2"
                    
                  >
                    Health
                  </a>
                  <a className="text-body" >
                    <small>Jan 01, 2045</small>
                  </a>
                </div>
                <a
                  className="h6 m-0 text-secondary text-uppercase font-weight-bold"
                  
                >
                  {obj.title}
                </a>
              </div>
            </div>))}

          {fitness.slice(0, 2).map((obj) => (
            <div
              className="d-flex align-items-center bg-white mb-3"
              style={{ height: 'fitContent' }}
            >
              <img className="img-fluid" src={obj.image.url} style={{ maxHeight: 100, maxWidth: 100, minHeight: 100, minWidth: 100 }} />
              <div className="w-100 h-100 px-3 d-flex flex-column justify-content-center border border-left-0">
                <div className="mb-2">
                  <a
                    className="badge badge-primary text-uppercase font-weight-semi-bold p-1 mr-2"
                    
                  >
                    Health
                  </a>
                  <a className="text-body" >
                    <small>Jan 01, 2045</small>
                  </a>
                </div>
                <a
                  className="h6 m-0 text-secondary text-uppercase font-weight-bold"
                  
                >
                  {obj.title}
                </a>
              </div>
            </div>))}
        </div>
      </div>
      {/* Popular News End */}
    </React.Fragment>
  )
}

export default Health
